$mainColor : #0a1d55;    

.red{
    background: $mainColor;
}

@mixin 함수() {
    background: #eee;
    padding: 20px;
    border-radius: 5px;
    max-width: 500px;
    width: 100%;
    margin: auto;
}

.my-alert{
    @include 함수();

    p{
        margin-bottom: 0;
    }
}

.my-alert2 {
    @extend .my-alert;//복붙
    background: #ffe591;
}




